<template>
    <Page title="Image Annotation Project" sourceCode="https://github.com/TJ-coding/yoloAnnotator">
        <SubHeading>
            Abstract
        </SubHeading>
        I wrote a program that can be used to annotate images. The program was designed to produce annotated files as training data for YOLO, a real time object detection model. YOLO requires training data to be in VOC, visual object classes format. The program was developed because I could not find a simple image annotation program that output annoated files in format required by yolo. The program was written in Python and it uses OpenCV and xml package.

        <SubHeading>
            Using the program
        </SubHeading>
        <SubSubHeading>Dependencies</SubSubHeading>
        OpenCV is required for the code to run. OpenCV can be installed by: <br>
        
        <span style="adding: .2rem .4rem;
        font-size: 87.5%;
        color: #fff;
        background-color: #212529;
        border-radius: .2rem">py -m pip install opencv-python.</span> <br>

        <SubSubHeading>Defining name of the objects</SubSubHeading>
        Name of the objects to be classified needs to be entered in the yoloAnnotator/labels.txt file. Multiple names can be entered by separating them using new line.<br>
        <br>
        <SubSubHeading>Installing images to be annotated</SubSubHeading>
        Images to be labelled need to be inserted to the yoloAnnotator/img folder. Any name can be used to store the image. The program supports jpg, jpeg and png image format.<br>
        <br>
        <SubSubHeading>Annotating images</SubSubHeading>
        To annotate images, run the yoloAnnotator/main.py program. Image to be annotated can be selected by moving the image slide bar. Name of the object to annotate can be selected by moving the label slide bar. The number on the label slide bar indicates the order of the name defined in the yoloAnnotator/labels.txt. I.e. if the first line in labels.txt file is plane and second line of labels.txt file is human, Label 0 indicates plane and Label 1 indicates human.<br>
        <br>
        Once the image and name are selected, an object can be annotated by left-clicking on the top left corner of the object and then left-clicking on the right corner of the object. Note, they do not need to start from top left and end at bottom right, they just need to be bounded the result would be equivalent.
        <CaptionImage src="projects/yolo/annotate.gif" imgHeight="60vh">Demonstration of how an object can be annotated by drawing a bounding box.</CaptionImage>
        <br>
        <br>
        <br>
        Annotation files are saved automatically whenever new image is selected or when the program quit.
        <div class="card">
            <SubSubHeading>Control schemes</SubSubHeading>
                <div class="card-body" style="padding-left: 3em">
                <strong>a</strong>: Select to previous image <br>
                <strong>d</strong>: Select to next image <br>
                <strong>k</strong>: Deleate a bounding box <br>
                <strong>q</strong>: Quit program <br>
            </div> 
        </div>
        <SubSubHeading>Finding annotated files</SubSubHeading>
            The images are annotated in VOC formats. The annotated files can be found in <span style="color:#89ACD7">yoloAnnotator/annotations</span> folder where the name of the file is the <span style="color:#89ACD7">name_of_the_image+"."+file_type+”.xml”</span>. For an example, a <span style="color:#89ACD7">“plane.jpg”</span> image should have a corresponding annotation file named <span style="color:#89ACD7">“plane.jpg.xml”</span>. The confusing name convention is to allow two files with same name but with different file type to be annotated. A correctly annotated file should look like the following:
        <br><br>
        <HideableCard title="yoloAnnotator/annotations/andrew-palmer-276540-unsplash.jpg.xml"> 
            <pre>
                &lt;?xml version=&quot;1.0&quot; ?&gt; 
                    &lt;annotation&gt; 
                        &lt;folder&gt;yoloAnnotator/img&lt;/folder&gt; 
                        &lt;filename&gt;andrew-palmer-276540-unsplash.jpg&lt;/filename&gt;
                        &lt;size&gt; 
                            &lt;width&gt;612&lt;/width&gt; 
                            &lt;height&gt;408&lt;/height&gt; 
                            &lt;depth&gt;3&lt;/depth&gt; 
                        &lt;/size&gt; 
                        &lt;object&gt; 
                            &lt;name&gt;plane&lt;/name&gt; 
                            &lt;pose&gt; &lt;/pose&gt; 
                            &lt;truncated&gt;0&lt;/truncated&gt; 
                            &lt;difficult&gt;0&lt;/difficult&gt; 
                            &lt;bndbox&gt; 
                                &lt;xmin&gt;79&lt;/xmin&gt; 
                                &lt;ymin&gt;131&lt;/ymin&gt; 
                                &lt;xmax&gt;465&lt;/xmax&gt; 
                                &lt;ymax&gt;267&lt;/ymax&gt; 
                            &lt;/bndbox&gt; 
                        &lt;/object&gt; 
                        &lt;object&gt; 
                            &lt;name&gt;plane&lt;/name&gt; 
                            &lt;pose&gt; &lt;/pose&gt; 
                            &lt;truncated&gt;0&lt;/truncated&gt; 
                            &lt;difficult&gt;0&lt;/difficult&gt; 
                            &lt;bndbox&gt; 
                                &lt;xmin&gt;364&lt;/xmin&gt; 
                                &lt;ymin&gt;149&lt;/ymin&gt; 
                                &lt;xmax&gt;406&lt;/xmax&gt; 
                                &lt;ymax&gt;195&lt;/ymax&gt; 
                            &lt;/bndbox&gt; 
                        &lt;/object&gt; 
                    &lt;/annotation&gt;

            </pre>

        </HideableCard>
        <SubHeading>Side note
        </SubHeading>
        Using the yoloAnnotator I created, I annotated 500 images of my face. Using these annotated data, I ran an additional training on a pre-existing weight file that was designed to detect faces. I did it so the model can detect my face better.
        <br>
        <br>
        By combining YOLO model and OpenCV I made a real time face masking program. I mapped colossal, a mascot of the university, onto my face.
        <CaptionImage src="projects/yolo/faceMask.gif">Demonstration of the sofware developed to mask faces on real time.</CaptionImage>
