        <SubHeading>
            Introduction
        </SubHeading>
        Chaotic attractors are a strange attractor and one of which is called a Lorenz attractor. 
        A system is defined to be chaotic if they system exhibits a sensitive dependence on the initial state. 
        Lorenz attractor is chaotic in many ways but one of them is in how the trajectory of particles diverge even if the start at a very similar position.
        <br><br>
        Chaos have a spiritual charm to it. 
        If all systems in the universe were non-chaotic, the future would be proportionally determinable that would mean if somethings are similar, they would always observe a relatively predictable outcome. 
        Mathematically said, f(x) ≈ f(x+dx).
        <br><br>
        However, in chaotic systems something that started in a similar state could give a drastically different outcome. 
        Therefore, f(x) ≈ f(x+dx) statement is not true for chaotic systems. 
        For an example, by definition of what is meant to be chaotic, you could argue that situations such as” I wouldn't have been born if my parents hadn't met at…” is due to the chaotic nature of the system, since the initial state of the system would have made a drastically different outcome.
        As can be seen on the animation, the path of the two particles diverge over time, even though the two particles start off from a similar position.
