var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('Page',{attrs:{"title":"Genetic Algorithm Project","sourceCode":"https://tj-coding.info/projectPage/genetic/index.html"}},[_c('CaptionImage',{attrs:{"src":"projects/genetics/simulation.gif"}},[_vm._v("A trained genetic algortihm controlling a charged particle across a defined path. Green cube represents the starting position of the particle, yellow cube is the finish position. The path are defeined by positive enforcement tokens, which are the gray cubes. As can be seen in the figure, after training, the genetic algorithm move the particle from green cube to the yellow cube.")]),_c('SubHeading',[_vm._v(" Abstract ")]),_vm._v(" A software that allow users to train a genetic algorithm model to control a charged particle under a defined path was developed using Python. The genetic algorithm uses three orthogonal capacitor plates to control the particle and the model will learn to apply an optimal amount of potential difference across the plates to move the charged particle along a defined path. "),_c('CaptionImage',{attrs:{"src":"projects/genetics/3plates.svg"}},[_vm._v("Figure1: Three orthogonal capacitor plates that are used to control the charged particle.")]),_vm._v(" The program allows users to design that the path and obstacles in the environment the model must navigate the charged particles through. The obstacles are entities that pose electric or magnetic field that affect the motion of the charged particle, such as a point charge that repel or attract the charged particle or magnetic field that bend the trajectory of the particle in the orthogonal direction to the field and motion. "),_c('CaptionImage',{attrs:{"imgHeight":"30vh","src":"projects/genetics/obsticleDesign.gif"}},[_vm._v("Figure2: User desigining obsticles in the enviroment by dragging and dropping a point charge."),_c('br'),_vm._v("The arows represents the direction of E field experienced by a test charge at the point.")]),_vm._v(" In this document, I will be highlighting some interesting aspects that compose the project along with some difficulties that I faced during the project. "),_c('SubHeading',[_vm._v("Context")]),_vm._v(" The project was written as a final project for the scientific computing skill course, hence the program was designed to fit the specifications of the assignment. "),_c('br'),_c('br'),_vm._v(" The specification of the project was to write a python program with a graphical user interface that incorporate a physical concept that was taught in the term. I was given three weeks to complete the project whilst other courses ran as usual. A report on the program was also required to be written using Latex before the deadline. "),_c('br'),_c('br'),_vm._v(" The specification forced me to take some unconventional design choices, such as the choices of libraries used in the project. The time constraint imposed a great deal of pressure on the project. As a result, the current state of the code in terms of cleanness and readability is at an embarrassing state. The code is currently being refactored and documented to resolve this issue. "),_c('SubHeading',[_vm._v("Feedback")]),_c('SubSubHeading',[_c('span',{staticClass:"material-icons"},[_vm._v("description")]),_vm._v(" The report submitted to the marker")]),_c('HideableCard',{attrs:{"title":"Unedited report"}},[_c('iframe',{staticStyle:{"width":"100%","height":"100vh"},attrs:{"src":"resources/genetics/genetic-project-report.pdf"}})]),_c('SubSubHeading',[_c('span',{staticClass:"material-icons"},[_vm._v("feedback")]),_vm._v(" The feedback written by the marker of the project")]),_c('HideableCard',{attrs:{"title":"Feedback"}},[_vm._v(" Report: "),_c('hr'),_vm._v(" Excellent and very detailed report. Especially good description of how the user interacts with this quite complex programme. Some typos e.g “4.3 Runner kutter method” Runge-Kutta, and then this section was blank. "),_c('br'),_c('br'),_vm._v(" GUI: "),_c('hr'),_vm._v(" Good set of instructions for users, some of the training does not work well (takes too long) but this is explained in the information. Very ambitious choice of project. "),_c('br'),_c('br'),_vm._v(" Code: "),_c('hr'),_vm._v(" Excellent code structure, with multiple sub programmes and clear classes. Most methods/functions did not have docstrings but overall commenting was good. "),_c('br'),_c('br'),_vm._v(" Mark: "),_c('hr'),_c('strong',[_vm._v("9/10")])]),_c('SubHeading',[_vm._v("Introduction")]),_c('SubSubHeading',[_vm._v("The Program")]),_vm._v(" As stated in abstract, the program allows users to build an environment with obstacles and defined path, then train the genetic algorithm to control a charged particle around a path using three orthogonal capacitor plates. "),_c('br'),_c('br'),_vm._v(" The capacitors accept a tape with series of potential difference to apply at a time sequence called control tape as an instruction program. The genetic algorithm model will be trained to find an optimal control tape for the capacitors. "),_c('CaptionImage',{attrs:{"src":"projects/genetics/3plates.svg"}},[_vm._v("Figure1: Three orhogonal capacitor plates.")]),_c('CaptionImage',{attrs:{"src":"projects/genetics/controlTape.svg"}},[_vm._v("Figure3: Representation of the control tape used to program the capacitor plates."),_c('br'),_vm._v("Verticle axis represents the dimension of plate. Horizontal axis represent the time to apply the potential difference.")]),_vm._v(" The program consists of environment builder tab and simulation tab."),_c('br'),_c('br'),_vm._v(" The environment builder tab is equipped with 4 views of the environment form different viewpoints that allow users to build a physical environment the model will be trained under using simple drag and drop method. This feature was challenging to implement and is discussed further in the challenges and mistakes section. "),_c('CaptionImage',{attrs:{"imgHeight":"50vh;","src":"projects/genetics/physicalEnviroment.gif"}},[_vm._v(" Figure demonstarating how \"build world\" tab can be used to build an enviroment. Top right figure represents the top view, bottom right is the front view and the bottom left represents the right view. By dragging and dropping on objects in the 2d figures, positions of the obsticles can be edited. ")]),_vm._v(" The simulation tab allows users to train the model and view how particle behave under control of the model. It is also equipped with features that shows the score a control tape achieved and a feature that graphically represents the control tape. "),_c('CaptionImage',{attrs:{"imgHeight":"50vh;","src":"projects/genetics/simulationTab.gif"}},[_vm._v(" Comparing difference in between two species from generation 0 and 2 species from generation 3000. Species from generation 0 is controlled in a random direction, whereas species from generation 3000 travel along the defined path. ")]),_vm._v(" It is important to note that quantum mechanical effects that would be present in a microscopic world is neglected in the simulation. "),_c('SubSubHeading',[_vm._v("Algorithms and equations implemented")]),_vm._v(" Some of the algorithms implemented in the project includes genetic algorithm, collision detection algorithm (SAT), Runge-Kutta method. The electromagnetic force equation was also implemented to simulate the behaviour of a charged particle under influence of electric and magnetic fields. Details of algorithm and equations used in the project is written under the \"Algorithm and Equations\" section. "),_c('SubSubHeading',[_vm._v("Libraries used")]),_vm._v(" Libraries used in the project are: "),_c('ul',[_c('li',[_vm._v("Numpy")]),_c('li',[_vm._v("Matplotlib")]),_c('li',[_vm._v("Tkinter")]),_c('li',[_vm._v("PIL")])]),_c('SubSubHeading',[_vm._v("Issues of the program")]),_vm._v(" As stated in the feedback, time taken to train the model is a problem. I have determined that most of the computing time is contributed by the Runge-Kutta method. GPU accelerated computing techniques can be employed to minimise this problem. More information on how this could be achieved is written in the “Extension” section. "),_c('br'),_c('br'),_vm._v(" The current program can only find a good optimal path when the defined path is simple. If the defined path is complex, then the improvement made by the genetic algorithm in each generation is small. How this problem may be solved is also explained in the “Extension” section. "),_c('SubSubHeading',[_vm._v("Challenges and mistakes")]),_vm._v(" Managing priorities of tasks were challenging due to the strict time constraint. It was managed using a pinboard and post note that represented a task to be completed. This extremely analogue method some how worked out to be extremely effective and allowed for an intuitive assessment of how much time should be invested into a task. "),_c('br'),_c('br'),_vm._v(" Some other challenges and mistakes experienced throughout the project include: "),_c('ul',[_c('li',[_vm._v("3D and 2D world viewers with drag and drop editing feature is not a feature included in the library. Therefore these features had to be custom developed.")]),_c('li',[_vm._v("Excessive use of object-oriented programming paradigm")]),_c('li',[_vm._v("Excessive use of direct access to attributes made from method of the same class")]),_c('li',[_vm._v("Reward scoring system is not adequate for training the model")])]),_vm._v(" More on these concepts are explored under the “Lessons learned” and \"Conclusion\" section. "),_c('SubHeading',[_vm._v("Algorithm and Equations")]),_c('SubSubHeading',[_vm._v("Genetic algorithm")]),_c('SubSubHeading',[_vm._v("Collision detection algorithm")]),_c('SubSubHeading',[_vm._v("Lorentz force law")]),_c('SubSubHeading',[_vm._v("Implicit and explicit iterative methods")]),_c('SubHeading',[_vm._v("Extension")]),_c('SubSubHeading',[_vm._v("Reducing training time")]),_vm._v(" Fourth-order Runge-Kutta method is implemented to simulate how the particle behave under electric and magnetic field. Runge-Kutta method have time complexity of O(n4) which is a polynomial time therefore, it is a computationally expensive time when n is large. The problem can be improved by employment of GPU accelerated computing techniques. For an example, there is a python library called Numba that can be used to accelerate python code with high use of iterations and NumPy operations. Alternatively, the Runge-Kutta part of the code can be replaced by parallel computing platform called CUDA. "),_c('SubSubHeading',[_vm._v("Improving results of training")]),_vm._v(" The program could only navigate charged particle successfully under a simple path. I hypostasise that the main cause of this issue is the rewarding system. The model is rewarded in a discrete manner. I.e. The genetic algorithm is awarded a constant score if it collects the token and if the token is not collected, the genetic algorithm is awarded nothing. This is problematic since small improvements made by the model is never awarded even if the direction of change made by the model is correct. "),_c('br'),_c('br'),_vm._v(" I believe that implementing a continuous rewarding system where reward is proportional to the distance between an enforcement token and the particle would improve the results of training. In addition, neuroevolutionary algorithm or gradient descent neural network can be implemented to improve the results of the training. Implementing such algorithm would allow the model to be trained to react to any environment made by the user rather than, being trained to solve a specific enviroment as it is in the current program. Improving the speed of the training, thus increasing the number of generations may also contribute towards a better result of training. "),_c('SubSubHeading',[_vm._v("Implementing remove feature in Build tab")]),_vm._v(" Even though remove button exist in the build tab, the functionality of the button currently does not exist. Current finite-state machine of the object editing feature looks like the following: "),_c('CaptionImage',{attrs:{"src":"projects/genetics/oldStateAutomata.svg"}}),_vm._v(" A new finite-state machine that would allow for the object removal feature in the build tab is devised. It remembers the previously selected object by adding a new state called \"Object was selected\" and therefore the remove state will know which object to remove. The newly devised finite-state machine is as follows: "),_c('CaptionImage',{attrs:{"src":"projects/genetics/newStateAutomata.svg"}}),_c('SubHeading',[_vm._v("UML Diagrams")]),_c('SubSubHeading',[_vm._v("Associations diagram")]),_c('CaptionImage',{attrs:{"src":"projects/genetics/uml/Associations.svg","setWhiteBackground":"true"}}),_c('SubSubHeading',[_vm._v("Inheritance diagram")]),_c('strong',[_vm._v("GUI inheritance diagram")]),_c('CaptionImage',{attrs:{"src":"projects/genetics/uml/gui.svg","setWhiteBackground":"true"}}),_c('strong',[_vm._v("World plotter inheritance diagram")]),_c('CaptionImage',{attrs:{"src":"projects/genetics/uml/worldPlotter.svg","setWhiteBackground":"true"}}),_c('strong',[_vm._v("Objects inheritance diagram")]),_c('CaptionImage',{attrs:{"src":"projects/genetics/uml/objectStructure.svg","setWhiteBackground":"true"}}),_c('strong',[_vm._v("EM simulation inheritance diagram")]),_c('CaptionImage',{attrs:{"src":"projects/genetics/uml/EM.svg","setWhiteBackground":"true"}}),_c('SubHeading',[_vm._v("Lessons learned")]),_c('SubSubHeading',[_vm._v("Object orieted programming")]),_vm._v(" Not everything should be a classes in Python. Only use classes if there is a clear data structure of the entity. "),_c('br'),_c('br'),_vm._v("Do not over use inheritance. It is the current trend to avoid using inheritance since it can create a spaghetti like code. Try to minimize the inheritance used in the program. "),_c('br'),_c('br'),_vm._v(" Try to avoid directly accessing attributes of a class from within the class, e.g. in pribate methods. Making variables required by a private method transparent by defining them as part of the parameter improve the readability of code. I think directly accessing attributes of same class from a public method is accceptable since it aligns with the encapsulation concept of object oriented programming. "),_c('SubSubHeading',[_vm._v("Only use tkinter for tiny projects")]),_vm._v(" Biggest advantage of tkinter is that tkinter is preinstalled in most python enviroments. Many libraries such as MayaVi do not have backend support for tkinter. I would recormend Kivy or PyQT for project that require modern libraries since they would have higher chance of being supported by other libraries. "),_c('SubSubHeading',[_vm._v("Never use matplotlib for real time three dimensional rendering")]),_vm._v(" Matplotlib is a library that was initially designed for two dimensional static plots. Even though animations and three dimentiaonl plots are supported, the library was not initially designed with these features in mind. As a result, animating three dimensional animation can be computationally costly and some features are not well supported. Use Mayavi for this kind of application since Mayavi was desgined with real time three dimensional plotting in mind. "),_c('SubSubHeading',[_vm._v("Runge-Kutta method has polynomial time complexity")]),_vm._v(" Polynomial time complexity solutions can quickly become intractable as number of items increase. Try using Numba or CUDA when implementing a Runge-Kutta methods. "),_c('SubHeading',[_vm._v("Conclusion")]),_vm._v(" I was able to apply genetic algorithm to control a charged particle with electric plates to direct them in a desired direction. I was also able to write an intuitive graphical user interface that allow users to build a simulation world with drag and drop procedure. "),_c('br'),_c('br'),_vm._v(" Even though genetic algorithm showed some improvement on the way at which it controls the charged particle, the algorithm could only solve trivial problems. I think the factors that contributed to such results are the lack of generations simulated and the way in which each control tapes are rewarded. In order to improve the result, I would use a continuous rewarding system rather than a discrete rewarding system. ")],1)}
